const PACKAGE_NAME = '@wix/auto_sdk_blog_tags';

/** Creates a new tag with the provided label if a tag with the same label doesn't already exist. */
export function createTag(payload: object): RequestOptionsFactory<any> {
  function __createTag({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.blog.v3.tag',
      method: 'POST' as any,
      methodFqn:
        'com.wixpress.npm.communities.platformized.blog.TagService.CreateTag',
      packageName: PACKAGE_NAME,
      url: resolveComWixpressNpmCommunitiesPlatformizedBlogTagServiceUrl({
        protoPath: '/v3/tags',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [{ path: 'tag.createdDate' }, { path: 'tag.updatedDate' }],
          },
        ]),
    };

    return metadata;
  }

  return __createTag;
}

